import { Component} from '@angular/core';
import { RouterOutlet } from '@angular/router';
import { BaseChartDirective } from 'ng2-charts';
import { ChartType, ChartData, ChartOptions } from 'chart.js';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { WebService } from './web.service';

@Component({
  selector: 'app-root',
  standalone: true,
  imports: [RouterOutlet, BaseChartDirective, FormsModule, CommonModule],
  templateUrl: './app.component.html',
  styleUrl: './app.component.css',
  providers: [WebService]
})
export class AppComponent {

  /*chartTypeFromForm is the user's choice of chart type, 
  default value is 'bar'*/
  chartTypeFromForm: ChartType='bar';
  /*openAbout is the user's choice of reading about page, 
  default value is false*/
  openAbout: boolean = false; 
  /*openAbout is the user's choice of using dark mode, 
  default value is false*/
  darkMode: boolean = false;
  //Chart specific properties
  chartType: ChartType = 'bar';
  chartData: ChartData= { datasets:[]};
  chartOptions: ChartOptions ={};
  languages : any=[];
  //Inject WebService to constructor of component
  constructor(private webService: WebService){

    //Observable start to emit data from stream
    webService.getLanguages().subscribe({
      //Success
      next: json => {
        //Assign the received data to this.languages
        this.languages = json;
        //Update the chart detail
        this.makeChart(this.languages, this.chartType, this.darkMode);
        console.log('Success loading data');
      },
      //Completed
      complete: () => {
        console.log('completed');
      },
      //Handle error
      error: e => {
        console.log(e.message);
      }
    });
  }
  
  updateChart(){
    //Update chartType according to the user's choice 
    this.chartType=this.chartTypeFromForm;
    //Update the chart detail
    this.makeChart(this.languages, this.chartType, this.darkMode);
  };

  makeChart(data: any[], chartType: String, darkMode: boolean) {
    let colorBW: string= 'black';
    /*I will use white color for labels, grid lines, title, and legends in dark mode,
      otherwise they will be black in color.*/
    if (darkMode){colorBW='white'}else{colorBW='black'};
    //Setting for bar chart
    if (chartType == 'bar') {
      this.chartData = {
        //Map the labels to language in json file
        labels: data.map((pair) => pair.language),
        datasets: [{
            //'Percent' will be shown in legend
            label: 'Percent',
            //Map the data to percent in json file
            data: data.map((pair) => pair.percent),
            /*I want to have rainbow color in bar chart.
              To do this, I need to pass the index (second parameter)*/
            backgroundColor: data.map(( pair,index) => paintRainbowColor(index)),
            borderColor: 'blue',
            borderWidth: 1
          }]
      };
      this.chartOptions = {
        responsive: true,
        maintainAspectRatio: false,
        indexAxis: 'y',
        plugins: {
          title: { display: true, text: 'Popular Programming Languages', color: colorBW },
          legend: { display: true, position: 'bottom', labels: { color: colorBW } }
        },
        scales: {
          y: {ticks: {color: colorBW}, 
              grid: {color: colorBW}},
          x: {ticks: {color: colorBW},
              grid: {color: colorBW}}
        }
      };
    } else {
      //Setting for pie chart
      this.chartData = {
        labels: data.map((pair) => pair.language),
        datasets: [{
            label: 'Percent',
            data: data.map((pair) => pair.percent),
            //I want to have rainbow color in pie chart.
            backgroundColor: data.map(() => paintRandomColor()),
            borderColor: 'blue',
            borderWidth: 1
          }]
      };      
      this.chartOptions = {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          title: { display: true, text: 'Popular Programming Languages', color: colorBW },
          legend: { display: true, position: 'bottom', labels: { color: colorBW } }
        },
        //Scales and indexAxis are meaningless for pie chart, so I remove them
        scales: {}  
      };
    }}
    //When user clicks info icon, set openAbout to true to display About page
    showAbout() {
      this.openAbout = true;
    }
    //When user clicks close button, set openAbout to false to display About page
    closeAbout() {
      this.openAbout = false;
    }
    /*When user checks Dark Mode checkbox, set darkMode to opposite value
      so that the 'dark-mode' class can be added to or removed from the first div element.*/
    switchMode() {
      this.darkMode = !this.darkMode;
      //Update the chart detail
      this.makeChart(this.languages, this.chartType, this.darkMode);
    }
}

  function paintRandomColor(): string {
    //Generate a random number up to 16**6 so that the whole range of color is covered.
    const n = Math.floor(Math.random() * (16**6)); 
    /*Change n to hex and add some '0' at the front if needed,
      since when n is small enough, the length of string may not be 6*/
    const color = `#${n.toString(16).padStart(6, '0')}`; 
    return color;
  }

  function paintRainbowColor(index: number): string {
    //Declare the colors for the bar chart
    const rainbowColors = [
      'red', 
      'orange',
      'yellow',
      'green',
      'blue', 
      'indigo', 
      'violet' 
    ];
    //Return the rainbow color in sequence according to index
    return rainbowColors[index % 7];
  }