import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
//@Injectable() allow this class can be injected into a component as a dependency
@Injectable()
export class WebService {
    //Inject HttpClient to constructor of WebService
    constructor(private http: HttpClient) {};
    /*Load program_languages.json.
      .subscribe() in app.component.ts needs an Observable object*/
    getLanguages(): Observable<any> {
        return this.http.get('program_languages.json');
    }
}